.card {
    flex-basis: calc((100% - 3 * 18px) / 3);
    margin: 9px;
}

.card__link {
    text-decoration: none;
    display: block;
    width: 100%;
    height: auto;
    border-radius: 5px;
}

.card__img {
    border-radius: 5px;
    width: 100%;
    height: 398px;
    margin-bottom: 8px;
}

.card__information {
    height: 32px;
}

.card__name {
    font-size: 12px;
    font-weight: inherit;
    text-transform: uppercase;
    color: black;
}